.linK:focus{
  border-bottom: 2px solid;
  border-image: linear-gradient(to right, #3B82F6, #10B981);
  border-image-slice: 1;
}

.linK:visited{
  border-bottom: 2px solid;
  border-image: linear-gradient(to right, #3B82F6, #10B981);
  border-image-slice: 1;
}